import "./styles.css";
import MyButton from "./Button";
import { useState } from "react";
import {
  BsFillArrowDownLeftSquareFill,
  BsFillExclamationTriangleFill
} from "react-icons/bs";

function Student() {
  return <h1 className="text-2xl">Studenti</h1>;
}

let human = {
  name: "Armela",
  age: 23,
  origin: "Saranda",
  getName: function () {
    return this.name;
  },
  getAge: function () {
    return this.age;
  },
  getOrigin: function () {
    return this.origin;
  }
};

let array = [
  { id: 1, name: "bicikleta", price: 100 },
  { id: 2, name: "motorra", price: 300 },
  { id: 3, name: "makina", price: 200 }
];

const products = array.map((a) => <li>{a.name}</li>);

function compareNumbers(a, b) {
  return a - b;
}

products.sort(compareNumbers);

let events = {
  handleClick: function () {
    alert(array[0].name);
  }
};

export default function App() {
  const [count, setCount] = useState(20);
  const [count2, setCount2] = useState(20);

  function handleClick() {
    setCount(count + 0.5);
  }
  function handleClick2() {
    setCount(count2 + 0.5);
  }

  return (
    <>
      <section>
        <div className="App">
          <h1 className="bg-purple-600">Hello CodeSandbox</h1>
          <h2>Start editing to see some magic happen!</h2>
          <MyButton count={count} onClick={handleClick} />
          <MyButton count={count2} onClick={handleClick2} />
          <Student />
        </div>
        <h1>{human.getName().toLowerCase()}</h1>
      </section>
      <h1>{"Mosha: " + human.getAge()}</h1>
      <h2>{"Origjina: " + human.getOrigin()}</h2>

      {products}

      <button
        onClick={events.handleClick}
        className="border border-2 border-purple-300 px-5 py-2 hover:bg-purple-400"
      >
        Click
      </button>

      <BsFillArrowDownLeftSquareFill />
      <BsFillExclamationTriangleFill />
    </>
  );
}
